import { Typography, Box, Container } from "@mui/material";
import { makeStyles, alpha } from "@material-ui/core/styles";
import React from "react";
import { cyan, yellow, blue, indigo, blueGrey } from "@mui/material/colors";

const Cyan = cyan[50];
const DCyan = cyan[700];
const BG = cyan[500];
const Yellow = yellow[500];
// const Yellow = yellow[300];
// const lightyellow = yellow[50];
// const Blue = blue[50];
const useStyles = makeStyles((theme) => ({
  home: {
    width: "100%",
    marginTop: theme.spacing(10),
    marginBottom: theme.spacing(9),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(3),
    [theme.breakpoints.down("sm")]: {},
  },
  hello: {
    width: "fit-content",
    height: "fit-content",

    // marginLeft: theme.spacing(10),
    color: theme.palette.common.white,
    textShadow: `0px 0px 10px ${alpha(theme.palette.common.black, 0.5)}`,
  },
  name: {
    width: "fit-content",
    height: "fit-content",
    color: alpha("rgb(242, 226, 169)", 0.95),
    textShadow: "0px 0px 10px rgba(0, 0, 0, 0.5)",
    // give text stroke
    // textStroke: `1px ${alpha(theme.palette.common.black, 0.5)}`,
  },
  homeBox: {
    width: "100%",
    display: "flex",
    flexDirection: "column",
    width: "fit-content",
    height: "52vh",
    // for smaller screens
    [theme.breakpoints.down("sm")]: {
      // height: "fit-content",
      // alignItems: "center",
      // justifyContent: "center",
    },
    justifyContent: "space-around",
  },
  fullstack: {
    // backgroundColor: cyan[300],
    backgroundColor: alpha(Cyan, 0.7),
    border: `1px solid ${alpha("rgb(204, 232, 247)", 0.65)}`,

    borderRadius: "5px",
    padding: ".25%",
    width: "fit-content",
    boxShadow: "0px 0px 10px 0px rgba(0,0,0,0.75)",
    color: alpha(theme.palette.common.white, 0.95),
    textShadow: `0px 0px 10px ${alpha(theme.palette.common.black, 0.5)}`,

    
    // borderRadius: "50%",
  },
  span: {
    color: alpha(Yellow, 0.7),
  },
}));

function Home() {
  const classes = useStyles();
  return (
    <Box className={classes.home}>
      <Box cn={classes.homeBox}>
        <Typography
          marginLeft={10}
          fontSize={"4.5rem"}
          className={classes.hello}
          variant="h3"
        >
          Hello, I'm
        </Typography>

        <Typography
          fontSize={["8rem", "rem", "3rem"]}
          variant="h1"
          className={classes.name}
        >
          {/* <span className={classes.span}> */}
          Booker King
        </Typography>

        <Box
          justifyContent={"flex-end"}
          sx={{
            // display: "flex",
            height: "fit-content",
            alignSelf: "flex-end",
            width: "fit-content",
          }}
        >
          <Typography
            fontStyle={"italic"}
            marginRight={6}
            fontWeight={1}
            flexDirection={"row"}
            // color={alpha(BG, 0.95)}
            // font size for different breakdown points
            fontSize={[".9rem", "1rem", "1.3rem"]}
            // marginTop={1}
            className={classes.fullstack}
            variant="h4"
            noWrap={true}
          >
            Full-Stack Web Developer
          </Typography>
        </Box>
      </Box>
    </Box>
  );
}

export default Home;
